.navbar {
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 9;
  background-color: #043b3f;
  box-shadow: 0 2px 7px rgba(255, 255, 255, 0.685);
  padding: 1rem 7%;
}
.navbar .logo {
  color: #ecc652;
  font-size: 2.5rem;
  font-weight: 700;
  display: flex;
}
.navbar .links{
  display: flex;
}
.navbar .links .single-link{
  color: #ecc652;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  padding: 0.5rem 1.5rem;
}
.navbar .links .single-link:hover{
  background-color: #607D8B;
}
.navbar-btn {
  display: none;
  color: #ecc652;
}

.navbar-hidden.hide {
  display: none;
}

.navbar-hidden.show {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: 9;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 4rem;
  color: #ecc652;
}

.navbar-hidden.show .close {
  position: absolute;
  top: 20px;
  right: 20px;
  color: red;
  font-size: 4rem;
  background-color: transparent;
  padding: 0;

}
.navbar-hidden.show .close:hover{
  background-color: transparent;
}
.navbar-hidden.show > *{
  margin: 4px;
  padding: 3px 30px;
  border-radius: 60px;
}
.navbar-hidden.show>*:hover {
  background-color: #043b3f;
  margin: 7px;
  text-align: center;
  border-radius: 40px;
  transform: scale(1.2);
}

@media (max-width: 520px) {
  .navbar .links {
    display: none;
  }
  .navbar-links {
    display: none;
  }
  .navbar-btn {
    font-size: 2.7rem;
    display: block;
  }
  .navbar-btn:hover {
    transform: rotate(270deg);
  }
}