.home{
  width: 100%;
  height: 100vh;
  background-position: left;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home::after{
  content: '';
  top: 0;
  left: 0;
  right: 0;
  position: absolute;
  background: #02262985;
  width: 100%;
  height: 100%;
}
.home *{
  z-index: 2;
}
.home-content h1{
  color: #ecc652;
  font-size: 5rem;
}
.home-content p{
  color: black;
  font-size: 2.2rem;
  padding: 0.5rem 0;
  line-height: 1.5;
}
.home-content .btn{
  padding: 20px 40px;
  background-color: #022629;
  color: #ecc652;
  font-size: 2rem;
  border-radius: 10rem;
  margin: 5px;
  box-shadow: 0 1px 5px rgba(255, 255, 255, 0.685);

}
.home-content .btn:hover{
  background-color: #ecc652;
  color: black;
}

@media (max-width: 800px) {
  .home-content {
    padding: 50px;
  }
}

@media (max-width: 520px) {
  .home-content .btn{
    padding: 10px 20px;
  }
  .home-content {
    padding: 20px;
  }

}