* {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  transition: all 0.4s linear;
  text-transform: capitalize;
  text-decoration: none;
  box-sizing: border-box;
}

html {
  scroll-padding-top: 5.5rem;
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-size: 62.5%;
}
html{
  font-size: 65%;
}
ul{
  list-style: none;
}
button{
  outline: none;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
a{
  text-decoration: none;
  color: inherit;
}

.dept-all, .level{
  padding: 80px 5rem;
  min-height: 100vh;
  background: #607D8B;
}
.dept-container{
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(35rem, 1fr));
  gap: 2rem;

}
.dept-container .single-dept{
  position: relative;
}
.dept-container .single-dept .box{
  padding: 1rem;
  overflow: hidden;
  height: 30rem;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  align-items: center;
  justify-content: center;
  display: flex;
  color: #ecc652;
  font-size: 2.5rem;
}
.dept-container .single-dept .box::after{
  content: '';
  top: 0;
  left: 0%;
  right: 0;
  position: absolute;
  background: rgba(0, 0, 0, 0.781);
  width: 100%;
  height: 100%;
}
.dept-container .single-dept .box > *{
  z-index:3;
}
.level-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(35rem, 1fr));
  gap: 3rem;
}
.level-container .single-level{
  height: fit-content;
  padding: 4rem;
  border-radius: 5rem;
  border: 1px solid #6e5d27c5;
  box-shadow: 0 8px 12px #000000c5;
}
.level-container .single-level:first-child{
  background: #5e0652;
}
.level-container .single-level:nth-child(2){
  background: rgb(124, 19, 19);
}
.level-container .single-level:nth-child(3){
  background: rgb(11, 11, 110);
}
.level-container .single-level:nth-child(4){
  background: #043b3f;
}
.level-container .single-level .icon{
  font-size: 3rem;
  color: white;
  text-align: right;
  font-weight: 800;
}
.level-container .single-level .level-courses{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.level-container .single-level .level-courses .course{
  width: fit-content;
  font-size: 1.8rem;
  border: 2px solid white;
  background-color: rgba(255, 255, 255, 0.637);
  margin: 2px;
  padding: 1rem 3rem;
  border-radius: 10px;
}
.level-container .single-level .level-courses .course:hover{
  background-color: black;
  color: white;
}


@media (max-width: 800px) {
  html{
    font-size: 55%;
  }
}
@media (max-width: 520px) {
  html {
    font-size: 50%;
  }
  
}